<template>
  <div>
    <h1>Full Name : {{ fullName }}</h1>
    <button type="button" @click="changeName">변경하기</button>
  </div>
</template>
<script>
export default {
  data() {
    return {
      firstName: "inseo",
      lastName: "jeong",
      fullName: "",
    };
  },
  watch: {
    firstName() {
      this.fullName = this.firstName + " " + this.lastName;
    },
    lastName() {
      this.fullName = this.firstName + " " + this.lastName;
    },
  },
  methods: {
    changeName() {
      this.firstName = "딱콩";
    },
  },
};
</script>
<style></style>
